@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body,
span {
  font-family: "Open Sans", sans-serif;
}
.container {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 100%;
    max-width: 100%;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
button {
  cursor: pointer;
  background: #ffffff;
  border: 2px solid #444444;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0 50px;
    max-width: 1150px;
    margin: 0 auto;
  }
}
.header__logo {
  text-align: center;
  padding: 22px 30px 17px;
}
.header__logo--mobile {
  max-width: 86px;
}
.header__logo--tablet {
  display: none;
}
.header__logo--desktop {
  display: none;
}
@media (min-width: 768px) {
  .header__logo {
    padding: 0;
  }
  .header__logo--mobile {
    display: none;
  }
  .header__logo--tablet {
    display: block;
    max-width: 113px;
    margin-top: 7px;
  }
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 -50px;
    padding: 0 50px;
    position: relative;
  }
}
@media (min-width: 1150px) {
  .header__logo {
    position: absolute;
    left: 0;
    right: 0;
  }
  .header__logo--mobile {
    display: none;
  }
  .header__logo--tablet {
    display: none;
  }
  .header__logo--desktop {
    display: block;
    max-width: 150px;
    margin: 9px auto 0;
  }
  .header__logo--desktop svg {
    transition-timing-function: ease-out;
    transition-duration: 0.1s;
  }
  .header__logo--desktop svg:hover {
    opacity: 0.6;
  }
  .header__logo--desktop svg:active {
    opacity: 0.3;
  }
}
.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 0 30px 15px;
}
.footer::before {
  content: "";
  position: absolute;
  left: 0;
  background-color: #f1f1f1;
  width: 100%;
  height: 160px;
  z-index: -1;
}
.footer__logo {
  display: none;
  cursor: pointer;
}
.footer__list {
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 142px;
  margin: 28px 0 25px;
}
.footer__link {
  color: #444444;
}
.footer hr {
  background-color: #e5e5e5;
  width: 260px;
  height: 1px;
  border: none;
}
.footer__copyright {
  margin-top: 21px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer__copyright b {
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  margin-right: 27px;
}
.footer__logo svg {
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.footer__logo svg:hover {
  fill: #63D1BB;
}
.footer__logo svg:active {
  opacity: 0.3;
}
.footer__link svg {
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.footer__link svg:hover {
  fill: #63D1BB;
}
.footer__link svg:active {
  opacity: 0.3;
}
.footer__copyright svg {
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.footer__copyright:hover svg {
  opacity: 0.6;
}
.footer__copyright:active svg {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    margin: 0 -50px;
    padding: 43px 50px 46px;
  }
  .footer::before {
    height: 143px;
  }
  .footer__logo {
    display: block;
  }
  .footer__list {
    margin-bottom: 0;
    margin-top: 0;
  }
  .footer hr {
    display: none;
  }
  .footer__copyright {
    margin-top: -5px;
  }
}
@media (min-width: 1150px) {
  .footer {
    padding: 42px 50px 46px;
  }
}
.nav__list {
  text-align: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.nav__item {
  padding: 27px 0;
}
.nav__link {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
}
.modal__container {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  top: 0;
  left: 0;
  display: none;
}
.modal--active {
  display: flex;
}
.modal {
  position: relative;
  margin: auto;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  padding: 29px 30px 46px;
  min-width: 320px;
}
.modal__title {
  text-align: center;
  text-transform: uppercase;
}
.modal p {
  font-weight: 400;
  font-size: 15px;
  line-height: 30px;
  color: #444444;
  margin-bottom: 21px;
  text-align: center;
}
.modal__size {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 30px;
}
.modal__size button.modal__size--active {
  border: 2px solid #63D1BB;
}
.modal__size button {
  border: 2px solid rgba(68, 68, 68, 0.1);
  padding: 16px 33px;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: #444444;
  outline: none;
}
.modal__button {
  background-color: #63D1BB;
  color: #ffffff;
  border: none;
  outline: none;
  padding: 18px 68px;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .modal {
    min-width: 566px;
    padding: 37px 102px 46px;
  }
  .modal__size button {
    padding: 16px 48px;
  }
}
.block__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin-bottom: 4px;
}
@media (min-width: 768px) {
  .block__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .block__title {
    font-size: 53px;
    line-height: 57px;
  }
}
.header-menu--mobile {
  display: block;
}
.header-menu--mobile .header-menu {
  position: relative;
}
.header-menu--mobile .header-menu__logo--tablet {
  display: none;
}
.header-menu--mobile .header-menu__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.header-menu--mobile .header-menu__item {
  max-width: 320px;
  width: 100%;
  padding: 27px 0 27px 76px;
  align-self: center;
  position: relative;
  border-bottom: 1px solid #e5e5e5;
}
.header-menu--mobile .header-menu__item:first-child {
  border-top: 1px solid #e5e5e5;
}
.header-menu--mobile .header-menu__item svg {
  position: absolute;
  left: 35px;
  opacity: 0.2;
}
.header-menu--mobile .header-menu__item a {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
}
.header-menu--mobile .header-menu__dropdown {
  display: none;
  position: absolute;
  top: 79px;
  left: 0;
  background-color: #ffffff;
  min-width: 320px;
  width: 100%;
  z-index: 2;
}
.header-menu--mobile .header-menu--visible {
  display: none;
}
.header-menu--mobile .header-menu__dropdown--active {
  display: block;
}
.header-menu--mobile .header-menu__toggle {
  position: absolute;
  top: 32px;
  right: 31px;
  display: flex;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}
.header-menu--mobile .header-menu__toggle span {
  background-color: #231f20;
  height: 2px;
  min-width: 20px;
}
.header-menu--mobile .header-menu__toggle span:nth-child(2) {
  margin-bottom: 4px;
  margin-top: 4px;
}
.header-menu__list-visible {
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin-top: -5px;
}
.header-menu__list-visible .header-menu__item {
  max-width: 320px;
  width: 100%;
  padding: 27px 0 27px 76px;
  align-self: center;
  position: relative;
  border-bottom: 1px solid #e5e5e5;
}
.header-menu__list-visible .header-menu__item:first-child {
  border-top: 1px solid #e5e5e5;
}
.header-menu__list-visible .header-menu__item svg {
  position: absolute;
  left: 35px;
  opacity: 0.2;
}
.header-menu__item a {
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  text-decoration: none;
  color: #000000;
}
.header-menu__item button svg {
  opacity: 0.2;
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.header-menu__item {
  cursor: pointer;
  z-index: 2;
}
.header-menu__item:hover button svg {
  fill: #63D1BB;
  opacity: 1;
}
.header-menu__item:active button svg {
  opacity: 0.3;
}
.header-menu__item button span {
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.header-menu__item:hover button span {
  opacity: 0.6;
}
.header-menu__item:active button span {
  opacity: 0.3;
}
.header-menu--tablet {
  display: none;
}
.header-menu--desktop {
  display: none;
}
.header-menu--bottom {
  display: none;
}
.header-menu__link {
  transition-timing-function: ease-out;
  transition-duration: 0.1s;
}
.header-menu__link:hover {
  opacity: 0.6;
}
.header-menu__link:active {
  opacity: 0.3;
}
.header-menu__toggle--close span:nth-child(1) {
  transform: rotate(45deg);
  margin-bottom: -1px;
}
.header-menu__toggle--close span:nth-child(2) {
  display: none;
}
.header-menu__toggle--close span:nth-child(3) {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .header-menu--mobile {
    display: none;
  }
  .header-menu--tablet {
    display: block;
  }
  .header-menu--tablet .header-menu__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style-type: none;
    width: 100%;
  }
  .header-menu--tablet .header-menu__item {
    border-left: 1px solid #ececec;
    padding: 32px 32px 32px 31px;
  }
  .header-menu--tablet .header-menu__item:first-child {
    margin-top: 3px;
    margin-left: 2px;
    padding-left: 32px;
  }
  .header-menu--tablet .header-menu__item:nth-child(2) {
    padding-right: 0;
  }
  .header-menu--tablet .header-menu__item button {
    border: none;
    background-color: #ffffff;
    font-weight: 700;
    font-size: 17px;
    line-height: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header-menu--tablet .header-menu__item button span {
    margin-left: 31px;
  }
  .header-menu--tablet__bottom {
    position: absolute;
    top: 100%;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 21px 50px 0;
  }
  .header-menu--tablet__bottom .header-menu__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    align-items: center;
  }
  .header-menu--tablet__bottom .header-menu__item:not(:last-child) {
    margin-right: 37px;
  }
  .header-menu--tablet__bottom .header-menu__link {
    text-decoration: none;
    color: #000000;
    font-weight: 700;
    font-size: 15px;
    line-height: 24px;
  }
  .header-menu--tablet__bottom span {
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: #444444;
  }
  .header-menu--visible {
    display: none;
  }
}
@media (min-width: 1150px) {
  .header-menu--desktop {
    display: block;
    z-index: 2;
  }
  .header-menu--desktop .header-menu__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    list-style-type: none;
    max-width: 405px;
    width: 100%;
  }
  .header-menu--desktop .header-menu__item:not(:last-child) {
    margin-right: 35px;
  }
  .header-menu--desktop .header-menu__link {
    font-weight: 700;
    font-size: 17px;
    line-height: 24px;
    text-decoration: none;
    color: #000000;
  }
  .header-menu--desktop .header-menu__item:last-child {
    border-left: 1px solid #efefef;
    height: 100%;
    padding: 32px 0 32px 35px;
  }
  .header-menu--tablet__bottom {
    display: none;
  }
  .header-menu--desktop__bottom {
    position: absolute;
    top: 100%;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 20px 50px;
  }
  .header-menu--desktop__bottom .header-menu__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    align-items: center;
  }
  .header-menu--desktop__bottom .header-menu__item:not(:last-child) {
    margin-right: 62px;
  }
  .header-menu--desktop__bottom .header-menu__link {
    text-decoration: none;
    color: #000000;
    font-weight: 700;
    font-size: 15px;
    line-height: 24px;
  }
  .header-menu--desktop__bottom span {
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: #444444;
  }
}
.zigzag-fill--desktop {
  display: none;
}
.zigzag-fill--tablet {
  display: none;
}
.zigzag-line--desktop,
.zigzag-line--tablet {
  display: none;
}
@media (min-width: 768px) {
  .zigzag-fill {
    margin-top: -12px;
    margin-left: -50px;
  }
  .zigzag-fill--tablet {
    display: block;
  }
  .zigzag-line--desktop,
  .zigzag-line--mobile {
    display: none;
  }
  .zigzag-line--tablet {
    display: block;
    margin-left: -50px;
  }
}
@media (min-width: 1150px) {
  .zigzag-fill {
    margin-top: -17px;
    margin-left: -50px;
  }
  .zigzag-fill--tablet {
    display: none;
  }
  .zigzag-fill--desktop {
    display: block;
  }
  .zigzag-line--tablet,
  .zigzag-line--mobile {
    display: none;
  }
  .zigzag-line--desktop {
    display: block;
  }
}
